import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpService } from 'src/services/http/http.service';
import { UtilityService } from 'src/services/utility/utility.service';
import { StorageService } from 'src/services/storage/storage.service';
import { MenuController, LoadingController } from '@ionic/angular';
import { FCM } from '@ionic-native/fcm/ngx';


@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  supplierDetails: any = [];
  pendingpurchaseOrders: any;
  status: any;
  grn: any;

  constructor(
    private router: Router,
    private httpService: HttpService,
    private utilityService: UtilityService,
    private storageService: StorageService,
    private menuCtrl: MenuController,
    public loadingController: LoadingController,
    private fcm: FCM

  ) { }

  ionViewWillEnter() {
    this.menuCtrl.enable(true);
  }
  async fcmcall(){
    // push notifications
    this.fcm.getToken().then(token => {
     console.log(token);
   });
    this.fcm.onTokenRefresh().subscribe(token => {
     console.log(token);
   });
    this.fcm.onNotification().subscribe(data => {
     console.log(data);
     alert(data);
     if (data.wasTapped) {
       console.log('Received in background');
       this.router.navigate(['/purchase-orders']);
     } else {
       console.log('Received in foreground');
       this.router.navigate(['/purchase-orders']);
     }
   });
  }
 
  ngOnInit() {
   // this.getSupplierdetails();
   this.getPPO();
   this.fcmcall();
  // this.getGRNCount();
  }
 getPPO() {
    // this.status = 'PROCESSED';
    this.status = 'PENDING';
    const url = '/supplier/purchaseOrders/' + this.status;
    this.httpService.get(url)
      .then((purchaseOrders) => {
        if (purchaseOrders.length > 0) {
          this.pendingpurchaseOrders = purchaseOrders.length;
          console.log('Saved Pending Purchase Orders ', this.pendingpurchaseOrders);
        }
      })

      .catch((error) => {
        console.log('Error', error);
      });
  }

  getPO(){
    this.router.navigate(['purchase-orders']);
    }
    getGRN(){
      this.router.navigate(['goods-receipt-notes']);
    }


}
